@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
body {
  margin: 0;
  padding: 0;
  color: rgb(57, 58, 75);
  font-family: "Poppins", sans-serif;
}

header h1 {
  text-align: center;
  margin: 100px 0 50px 0;
  font-size: 3rem;
}

main {
  text-align: center;
}

.btn {
  margin: 10px;
  padding: 15px 20px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: -1px 2px 4px #848484;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
}

.btn:hover {
  background-position: right center; /* change the direction of the change here */
}

.btn-1 {
  background-image: linear-gradient(
    to right,
    #f6d365 0%,
    #fda085 51%,
    #f6d365 100%
  );
}

.random__color__section {
  box-shadow: 0 0 5px 0 #00000061;
  width: 500px;
  margin: 100px auto 30px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.color__block {
    height: 100%;
    width: 25%;
}


.color__info {
    opacity: 0;
    transition: .4s ease-in-out;
    cursor: context-menu;
}

.color__block:hover > .color__info {
    opacity: 1;
}

.like__btn {
    position: absolute;
    right: -100px;
    cursor: pointer;
    width: 45px;
    opacity: .2;
    transition: .5s;
}

.like__btn:hover {
    opacity: .6;
}


.create__color__title {
  cursor: pointer;
  opacity: 0.4;
  transition: 0.4s;
}

.create__color__title:hover {
  opacity: 1;
}
.create__colors__section {
  height: 145px;
  position: relative;
}

.create__color__block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
}

.user__color {
  width: 50px;
  height: 50px;
  background: #ffffff;
  margin: 0 10px;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0px 1px 6px 0 #565656;
  transition: .5s;
}

.selected__color {
  padding: 5px;
  margin: 0 5px;
}

.save__user__modal {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -30px;
}

input[name="user-name"] {
  padding: 2px 5px;
  background: transparent;
  border: 0;
  box-shadow: 1px 1px 4px 0 #00000061;
  outline: none;
}

input[name="user-name"]:focus {
  box-shadow: 1px 1px 6px 0 #8d773c;
}

button[name='user-send'] {
  background: #f6cf67;
  border: none;
  box-shadow: 0 0 2px 0 #000;
  padding: 2px 7px;
  margin: 0 10px;
  cursor: pointer;
  color: white;
}


/* окно диалога */

p.speech {
  position: fixed;
  padding: 7px;
  left: 0;
  right: 0;
  width: 150px;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 2px 2px 4px #888;
  margin: 0 auto;
  margin-top: -40px;
  display: none;
}


.liked__colors__section {
    position: absolute;
    left: 45px;
    opacity: .5;
    cursor: pointer;
    transition: 1.5s;
    width: 200px;
    top: 80%;
}
.liked__colors__section:hover {
    opacity: 1;
}

.liked__colors__header {
    display: flex;
    justify-content: center;
    align-items: baseline;
}
.liked__colors__header img {
    width: 35px;
    padding: 0 10px;
    z-index: 20;
}

.linked_h3 {
    -webkit-transform: translateX(-85px);
            transform: translateX(-85px);
    opacity: 0;
    transition: .7s;
}

.liked__colors {
    opacity: 0;
    transition: .6s;
    overflow: auto;
    max-height: 100%;
    box-shadow: 1px 3px 6px 0 #0006;
    max-height: 400px;
    display: none;
}
.liked__colors_show {
    display: block;
}
.l_colors {
    display: flex;
    padding: 2px 0;
}

.users__colors_db {
    position: absolute;
    top: 40px;
    right: 100px;
    background: white;
    padding: 2px 15px;
    cursor: pointer;
    opacity: .3;
    transition: .5s;
    box-shadow: 1px 1px 10px 0px #a3a3a3;
    -webkit-animation: 1s 3 blink;
            animation: 1s 3 blink;
}

@-webkit-keyframes blink {
    0% {
        opacity: .3;
        box-shadow: 1px 1px 10px 0px #477de2;
    } 50% {
        opacity: 1;
    }
    100% {
        opacity: .3;
    }
}

@keyframes blink {
    0% {
        opacity: .3;
        box-shadow: 1px 1px 10px 0px #477de2;
    } 50% {
        opacity: 1;
    }
    100% {
        opacity: .3;
    }
}

.users__colors_db:hover {
    opacity: 1;
}

.colors__Db {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.users__colors__section {
    height: 0;
    overflow: auto;
    transition: .3s;
    width: 200px;
}

.popular__colors__title:hover + .users__colors__section,
.users__colors__section:hover {
    height: auto;
    max-height: 550px;
}


.preview__sections {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background: linear-gradient( 
44deg
 , #de02bb, #a137ed, #26e7e8);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.preview__sections h1 {
    font-size: 5rem;
    color: aliceblue;
    font-weight: 500;
    font-family: 'Poppins';
    margin: 0;
    margin-top: -130px;
}

.preview__sections p {
    color: #f0f8ffcf;
}
